import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { ApiService } from 'src/app/shared/services/api.service';
import { AuthService } from 'src/app/shared/services/auth.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  loginForm:any;
  submitted:boolean=false;
  constructor(
    private _toastr:ToastrService,
    private _authService:AuthService,
    private  _apiService:ApiService,
    private fb:FormBuilder,
    private _router:Router
  ) { }

  ngOnInit(): void {
    this.createForm();
  }

  createForm(){
    this.loginForm = this.fb.group({
      username:['',Validators.required],
      password:['',Validators.required]
    })
  }

  login(){
    this.submitted=true;
    if(!this.loginForm.valid) return;
    this._apiService.Post('auth','login',this.loginForm.value).subscribe((res:any)=>{
      if(res.success){
        if(res.data.isAdmin === 0){
          this._toastr.error('No record exists');
          this.loginForm.reset();
          this.submitted=false;
        }
        else{
          this._authService.saveUser(res.data);
          this._toastr.success('Welcome Admin!');
         
          this._router.navigateByUrl('/dashboard');
        }
        
      }
      else{
        this._toastr.error(res.message);
      }
    },err=>{
      this._toastr.error('Connection Problem');
    })
  }

}
